<template>
    <div>
        <mhead-top titleHead="logo" lfIcon="el-icon-s-home icon" lfUrl="/home" rgIcon="el-icon-user-solid icon" rgUrl="/userCenter/index/index"></mhead-top>
        <div class="mBg">
            <div class="regBox">
                <div class="register-panel">
                    <h2 class="tc">绑定用户信息</h2>
                    <el-form :model="form" :rules="rules" ref="form" label-width="80px" class="mt15">
                        <el-form-item label="用户名" prop="username">
                            <el-input type="text" v-model="form.username" placeholder="请输入用户名"></el-input>
                        </el-form-item>
                        <el-form-item label="密码" prop="password">
                            <el-input type="password" v-model="form.password" placeholder="请输入密码"></el-input>
                        </el-form-item>
                        <el-form-item label="确认密码" prop="checkpassword">
                            <el-input type="password" v-model="form.checkpassword" placeholder="请再次输入密码"></el-input>
                        </el-form-item>
                        <el-button type="primary" @click="submitForm('form')" class="w">确定</el-button>
                    </el-form>
                </div>
                <div class="loginP_bottom tc">已有账号？<router-link class="colorGreen" :to="{path:'/login/login',query:{}}">登录</router-link></div>
            </div>
        </div>
    </div>
</template>


<script>
    import {writeUserInfo} from '../../service/api'
    import {setStore,getStore} from '../../config/mUtils'
    import mheadTop from '../../components/mobile/headTop';
    import {mapState, mapMutations, mapActions} from 'vuex'
    export default {
        name: 'mUserInfo',
        data(){
            return{
                form:{
                    username:'',
                    password:'',
                    checkpassword:'',
                    mobile:''
                },
                rules:{
                    username: [
                        { required: true, type: 'string', message: '请输入用户名',trigger: 'blur'}
                    ],
                    password:[
                        {required: true, type: 'string', message: '请输入密码', trigger: 'blur'}
                    ],
                    checkpassword:[
                        {required: true, type: 'string',message: '请再次输入密码' , trigger: 'blur'}
                    ]
                },
            }
        },
        components:{
            mheadTop
        },
        mounted(){
            this.initData();

        },
        computed:{
            ...mapState([
                'userInfo','login'
            ])

        },
        methods:{
            ...mapActions([
                'getUserInfo'
            ]),
            ...mapMutations([
                'RECORD_USERINFO',
            ]),
            async initData(){
                this.form.mobile = this.$route.query.mobile;
                console.log(this.form.mobile);
                if(!this.form.mobile){
                    var mobile = getStore('mobile');
                    this.form.mobile = mobile;
                }
            },
            submitForm(formName) {
                this.$refs[formName].validate(async (valid) => {
                    if (valid) {
                        if(this.form.password == this.form.checkpassword){
                            var res = await writeUserInfo(this.form.username,this.form.password,this.form.mobile);
                            console.log(res);
                            if(res){
                                setStore('token', res.token);
                                this.$message({
                                    message: res.msg,
                                    type: res.status==12000?'success':'warning'
                                });

                                if(res.status==12000){
                                    this.getUserInfo();
                                    var self = this;
                                    setTimeout(function(){
                                        self.$router.push({path:"/home"});
                                    },1000);
                                }
                            }

                        }else{
                            this.$message({
                                message:'两次输入密码不一致',
                                type:'warning'
                            })
                        }
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },

        },
        created(){

        },

    }

</script>

<style lang="scss">
    @import '../../style/mixin';
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        height: 100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    /*注册*/
    .regBox{
        width:px2rem(750);
        margin-top: px2rem(20);
    }
    .regBox h2,.regBox h3{font-size:28px;font-weight: bold;line-height: 36px;color:#0bb8b2;}

    .register-panel{
        background-color: #fff;
        padding:px2rem(20);
        flex-shrink: 0;
    }
    .loginT{font-size: 16px;text-align: center;width:50%;line-height: 40px;cursor: pointer;}
    .loginT.active{border-bottom: 3px solid #0bb8b2;}
    .loginPhone{border:1px solid #ddd;height: 40px;line-height: 40px;margin-top:15px;padding:0 15px 0 45px;}
    .loginPhone input{height: 40px;line-height: 40px;}
    .loginPhone img{width: 24px;height: 24px;position: absolute;top:8px;left: 10px;}
    .loginInput img{width: 24px;height: 24px;position: absolute;top:8px;left: 10px;}
    .loginCode{border:1px solid #ddd;height: 40px;line-height: 40px;padding:0 15px 0 45px;flex-shrink: 1;}
    .loginCode input{height: 40px;line-height: 40px;}
    .loginCodeBtn{background-color: #0bb8b2;color:#fff;height: 40px;line-height: 40px;padding:0 30px;flex-shrink: 0;}
    .loginBtn{background-color: #0bb8b2;color:#fff;height: 40px;line-height: 40px;width:100%;text-align: center;display: block;margin-top: 15px;font-size: 16px;}
    .loginCodeBtn:hover,.loginBtn:hover{background-color:#0a9e9b;color:#fff;}
    .loginP_bottom{
        background-color: #ddd;color:#333;
        line-height: 60px;
        position: fixed;
        bottom:0;
        left: 0;
        width:px2rem(750);
    }
    .loginP_bottom a:hover{text-decoration: underline;}




</style>



